@import url(https://fonts.googleapis.com/css2?family=Jost:wght@600&display=swap);
:root{
	--nav-compare: #b5eecd;
	--nav-manual: #f8c1ba;
	--nav-calculator: #b7dbf3;
}

html,body, #root, .App{
  height: 100%;
  width:100%;
}

.App>.alert{
	position: fixed;
    margin-top: 5rem;
    margin-right: 1rem;
    margin-left: 1rem;
    z-index: 5000;
}

/***********************************/
.item_icon{
	border: 0.2rem solid;
	border-radius: 0.2rem;
	height: 2em;
	width: 2em;
	display: block;
	background-color: #333;
}
.item_name{
	margin-left: -0.7em;
	line-height: 2em;
}
.ItemConfig{
	padding-top: 3px;
	overflow: auto;
    height: 100%;
    max-height: 18em;
}
.ItemConfig .accordion-button{
	padding: .5rem 1rem;
	padding-left: 0;
}
.ItemConfig .accordion-body{
	padding:0;
}

/*************************/
.white{
	color: white;
	border-color: white;
}
.blue{
	color: #116dfb;
	border-color: #116dfb;
}
.green{
	color: green;
	border-color:green;
}
.orange{
	color: orange;
	border-color: orange;
}


/**************** MAIN *****************/
main{
	height: max-content;
	width:100%;
	position: fixed;
	margin: 0 !important;
	padding: 0 !important;
	position: relative;
}
section{
	width:100%;
	min-height: 90vh;
	margin:0 !important;
	padding:0 !important;
	padding-top: 5% !important;
	overflow: auto;
	padding-bottom: 3em !important;
}
.bg-image{
	background-attachment: fixed;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	align-items: center;
	justify-content: center;
	height: 100%;
}
.bg-image-pa{
	background: inherit;
}
.container-blur {
 height: max-content;
 position: relative;
 box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
 background: inherit;
 overflow: hidden;
 margin-top: 1em;
 margin-bottom: 1em;
 padding: 0;
}
.container-blur:before {
  content: '';
  background: inherit;
  position: absolute;
  overflow: hidden;
  z-index: 0;
  filter: blur(6px);
  margin: 0;
  height:100%;
  width:100%;
}

/************ OVERRIDE BOOTSTRAP *********************/

.form-check-input:checked {
    background-color: #0d6efd;
    border-color: #0d6efd;
}
.form-check{
	margin-right: 1em;
	height: auto;
}
.accordion-body{
	position:relative;
}
.accordion-item:last-of-type .accordion-collapse{
	border-bottom-width: 0px;
}

.form-control:focus{
	box-shadow: 0 0 0 0.25rem rgba(115, 115, 115, 0.41);
}
.Header{
	width:100%;
	position: relative;
	top:0;
	z-index:500;
}
.Logo{
	width: 2em;
    height: auto;
    margin-left: 0.4em;
    margin-right: 0.4em;
}
/************************************************/
.nav-link{
 	cursor: pointer;
 	z-index: 10;
}
.Navv{
	transition: border-bottom 0.5s ease;
	border-bottom: 2px solid;
	background-color: antiquewhite;
}
.nav-floor{
	height: 100%;
	position: absolute;
	bottom: 0;
	transition: all 0.5s ease;
	z-index:5;
	margin-left: 184px;
    width: 68px;
}
.Navv.manual{
	border-color: var(--nav-manual);;
}
.Navv.manual .nav-floor{
	background-color: var(--nav-manual);
}
.Navv.calculator{
	border-color: var(--nav-calculator);;
}
.Navv.calculator .nav-floor{
	background-color: var(--nav-calculator);
}
.Navv.compare{
	border-color: var(--nav-compare);
}
.Navv.compare .nav-floor{
	background-color: var(--nav-compare);
}
/*************************************/
.Config{
	transition: max-width 0.45s ease;
	background-color: black;
	position:absolute;
	right: 0;
	margin: 0;
	margin-left: auto;
	top: 2em;
	overflow: hidden;
	height: max-content;
	max-width: 50%;
	z-index: 1000;
}
.Config.hidden{
	max-height: 3em;
	max-width: 4em;
}
.ConfigIcon{
	width: 2em;
	height: 2em;
	display:block;
	margin:0;
	margin-top:0.5em;
	margin-bottom: 0.5em;
	border: 1px solid;
	cursor: pointer;
	border:none;
}
.spin{
	animation: spin 3s infinite linear;
}
@keyframes spin{
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(1turn);
  }
}

.alert-container{
	position: absolute;
    height: 100%;
    right: 0.7em;
}
.alert-div{
	position: sticky;
	top:0;
}
/****************************/
.Zoom{
	margin-top:0.2em;
	margin-bottom:0.2em;
}
/******************************/
.Lang .col-auto{
	padding-left: 0.1em;
	padding-right: 0.1em;
}
.lang-input{
	position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
}
.lang-input:checked+.lang-lbl{
	border: 0.2em solid;
	border-color: var(--bs-primary);
	margin:0;
	padding-top: 0;
	padding-bottom: 0;
}
.lang-lbl{
	cursor: pointer;
	border: 0.1rem solid #9e9e9e;
	box-sizing: content-box;
	margin-left:0.3em;
	margin-right:0.3em;
	padding-bottom: 0.1rem;
	border-radius: .5rem;
}


.SectionManual{
	background-image: url(/eidolon-exp/static/media/bg-manual.a3092b75.png);
	background-position: right center;
}
.SectionManual h1, .SectionManual h4, .SectionManual h6{
	padding-left: 1rem;
	color: #0d6efd;
}
.card-manual{
	background-color: rgba(255,255,255,0.3);
}
.card-manual .card-header{
	background-color: rgba(170,170,170,.2);
}

.SectionManual img{
	display: block;
    border: 2px solid #141619;
    margin: auto;
}

.SectionCalc{
	background-image: url(/eidolon-exp/static/media/header-bg.f4397451.jpg);
}

.card-calc{
	height:100%;
	background: rgba(0,0,0,0.3);
}
.card-calc .card-header{
	background-color: rgba(0,0,0,.2);
}


/************* SPECIAL INPUT  *******************/

.fake-input-container{position: relative;}
.fake-input{
	font-family: 'Jost', sans-serif;
	position: absolute;
	height: 100%;
	background-color: rgba(0,0,0,0);
	top:0;
}
.input-dark, .input-dark:focus{
	font-family: 'Jost', sans-serif;
	background-color: #3c3c3c;
	border-color: black;
	color: #ffd639;
}
.input-group-text.input-dark{
	background-color: #131212;
    color: white;
    border-color:black;
}
.fake-input-placeholder{
	color: #6c757d;
}
.fake-input-input{
	position:relative;
	z-index: 50;
}
.fake-input-input{
	background-color: rgba(0,0,0,0)!important;
	color: rgba(0,0,0,0)!important;
	border-color:black!important;
}
.fake-input-value{
	background: none;
}
.fake-input-value{
	background: none;
	background-color: rgba(0,0,0,0);
}
.fake-input-value span{
	background-color: #3c3c3c;
}
.fake-input-value span, .fake-input-placeholder span{
	text-align: center;
	letter-spacing: 1px;
	text-transform: full-width;
	transform:scale(15.0,1.0);
}
.fake-input .dec{font-size: 0.7em;}
.input-cursor span:last-child:after{
	content:'';
	display: inline-block;
	width: 2px;
	height: 1.3em;
	padding-left: 1px;
	margin:0;
	background:none;
	border-right: 1px solid #ffd639;
	font-family: 'Jost', sans-serif;
	animation: blink-animation 1s steps(5, start) infinite;
	-webkit-animation: blink-animation 1s steps(5, start) infinite;
}
@keyframes blink-animation {
    to{ visibility: hidden; }
}


/************** NEEDS *********************/

.ItemNeed .pack .badge{
	color: yellow;
}

.need-btns{
	flex-direction: row-reverse;
	margin: auto;
}
.need-btn{
	border: none;
	font-size: 1.1em;
	width: max-content;
	border-radius: 3em;
	font-family: monospace;
	margin: 0.2em;
	background-color: #2779f9;
}
.need-list-item{
	background-color: rgba(17,17,17,0.8);
	border-color: rgba(150,150,150,0.13);
}
.need-list-item .item_name{
	font-size: .875em;
}

.SectionCompare{
	background-image: url(/eidolon-exp/static/media/bg-compare.ffff0a3e.jpg);
	background-position: 23% center;
}
.card-compare{
	background-color: rgba(76, 175, 80, 0.3);
	background-color: rgba(0,0,0,0.3);
	color: white;
}
.card-compare .card-header{
	background-color: rgba(100,100,100,.2);
}
.Compare .Item{
	font-size: .875em;
}
.select-item{
	display: inline-block;
}
.btn-select-item{
	background: aliceblue;
    color: blue;
    border-color: #ced4da;
}
.btn-select-item:hover{
	background-color:#e9ecef;
	color: blue;
}
.Compare.container-blur{
	overflow: visible;
}
.Compare.container-blur:before{
	height:100%;	
}
.Compare .pack .badge, .Compare .badge{
	color:black;
	font-size: 1em;
}
.Compare .dropdown-item.active, .dropdown-item:active{
	background-color:#73c1ff;
}

.IsEqualsTo{
    padding-bottom: .3em;
    color: #3737b9;
}
.IsEqualsTo .badge{
	color: white;
}
.equals-to-text{
    padding-left: 0;
    padding-right: 0;
}
.equals-to-arrow{
	height: 2em;
	width: 2em;
	margin-bottom: .5em;
	color: white;
    margin-left: 0.2em;
}
.equals-to-arrow-down{display: none;margin: auto;}

.Compare button{
	background-color:#e9ecef;
}
.Compare input{
	font-family: 'Jost', sans-serif;
}
.Footer{
	height: max-content;
	width:100%;
	background-color: #e7b8ff;
	background-color: #f8c1ba;
	font-size: .8em;
	border-top: 2px solid black;
}
.icon_logo{
	height:1em;
	width:auto;
	margin-left: 0.1em;
	margin-right: 0.1em;
}
.name_logo{color: #62beca;}
.cm_logo{
	margin-left: 0.4em;
	margin-right: 0.2em;
}
.footer_me{
	background-color: black;
	color:white;
}
.contador-container>*{display: inline-block;}
.contador{
	margin:0.2em;
	margin-right:0.5em;
}
.thanks{
	color:#673ab7;
	text-decoration: none;
}

/* Portrait tablet to landscape and desktop */
@media (min-width: 768px) and (max-width: 979px) {

	.Config{
		max-width: 70%;
	}
	.Config.hidden {
    	max-width: 4em;
	}

}

/* Landscape phone to portrait tablet */
@media (max-width: 767px) {
	.Header{
		position:fixed;
	}
	section{
		padding-top: 5em!important;
	}
	.expand-md-hidden {
    	display: none;
	}

	.Config{
		max-width: 87%;
	}
	.Config.hidden {
    	max-width: 4em;
	}

	.CompareGral .table .item_name{
		display: none;
	}
	.IsEqualsTo .equals-to-arrow-down{display: block;}
}

/* Landscape phones and down */
@media (max-width: 480px) {
	.expand-sm-hidden {
    	display: none;
	}
	.Config{
		max-width: 95%;
	}
	.Config.hidden {
    	max-width: 4em;
	}
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width : 321px) { 
	.ItemConfig{max-height: 13em;} 
}

/* iPads (landscape) ----------- */
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) { 
	.ItemConfig{ max-height: 14em; } 
}

/* iPhone 4 and high pixel ratio devices ----------- */
@media
only screen and (-webkit-min-device-pixel-ratio : 1.5),
only screen and (min-device-pixel-ratio : 1.5) { 
.ItemConfig{max-height: 13em;} 
}
